// =============================================================================
// > MODULES - FORMS: LOGIN
// =============================================================================

#loginform {
    overflow: hidden;
    clear: both;
    position: relative;
    max-width: 400px;
    margin: 0 auto;

    label {
        width: 100%; }

    p {
        margin-bottom: 10px;

        #user_login,
        #user_pass {
            // +size(100%, 40px)
            // +font(15px 40px bold)
 }            // padding: 0 10px

        &.login-remember {
            float: left;
            width: 40%;
            @include screen(xs) {
                width: 100%; }
            label {
                height: 50px;
                line-height: 50px;
                @include font(14px); } }

        &.login-submit {
            width: 60%;
            float: right;
            @include screen(xs) {
                width: 100%; }
            input {
                margin-top: 0; } }

        &.login-password a {
            float: right;
            @include font(12px 2 light secondary);
            &:hover {
                @include font(primary); } } } }



