// =============================================================================
// > TYPOGRAPHY - TITLES
// =============================================================================

%h {
    font-weight: normal;
    text-rendering: geometricPrecision;
    margin: 1.5em 0 1em; }


// ==================================================
// > H1
// ==================================================
h1:not([class]),
.h1 {
    @extend %h;
    @include font(40px bold); }



// ==================================================
// > H2
// ==================================================
h2:not([class]),
.h2 {
    @extend %h;
    @include font(35px bold); }



// ==================================================
// > H3
// ==================================================
h3:not([class]),
.h3 {
    @extend %h;
    @include font(30px bold); }



// ==================================================
// > H4
// ==================================================
h4:not([class]),
.h4 {
    @extend %h;
    @include font(24px bold); }



// ==================================================
// > H5
// ==================================================
h5:not([class]),
.h5 {
    @extend %h;
    @include font(20px bold); }



// ==================================================
// > H6
// ==================================================
h6:not([class]),
.h6 {
    @extend %h;
    @include font(16px bold); }



