// =============================================================================
// > LAYOUTS: MODULES - NAVIGATION: PAGINATION
// =============================================================================

.pagination-walker {
    text-align: center;
    padding: 40px 0;


    // ========== DIRECTIONS ========== //
    &__direction {
        @include size(40px);
        @include font(18px 35px primary center);
        position: relative;
        border-radius: 50%;
        border: 2px solid color(primary);
        display: inline-block;
        text-indent: -9999px;
        transition: all .25s ease;
        margin: 0 10px;

        &::before {
            text-indent: 0;
            @extend %cover; }
        &--first:before {
            @include fa(angle-double-left); }
        &--previous:before {
            @include fa(angle-left); }
        &--next:before {
            @include fa(angle-right); }
        &--last:before {
            @include fa(angle-double-right); }

        &--first,
        &--last {
            @include screen(xs) {
                display: none; } }

        &:hover {
            color: white;
            border-color: color(primary);
            background: color(primary); }

        &[disabled] {
            border-color: darken(color(primary), 20%);
            color: darken(color(primary), 20%);
            background: none;
            opacity: .2; } }

    // ========== PAGES ========== //
    &__pagelist {
        display: inline-block;

        &__item {
            display: inline-block;
            position: relative;

            &__current,
            &__link {
                @include font(20px dark-grey bold);
                padding: 0 30px;
                @include screen(xs) {
                    padding: 0 10px; }
                &:hover {
                    @include font(primary); } }

            &__current {
                @include font(primary); }

            &:after {
                content: "";
                @include size(20px, 2px);
                background: darken(color(light-grey), 20%);
                @include absolute(right -10px top 50%);
                margin-top: -1px;
                @include screen(xs) {
                    @include size(3px, 2px);
                    right: -3px; } }

            &:last-child:after {
                display: none; } } }

    // ========== LOCATION ========== //
    &__location {
        @include font(14px bold medium-grey);
        margin: 0; } }



