// =============================================================================
// > MODULES - GUI: SHADOWBOX
// =============================================================================

.shadowbox {
    @extend %cover;
    position: fixed;
    background: rgba(black, .75);
    z-index: 9999;
    display: none;

    &__content {
        @extend %cover;
        display: flex;
        align-items: center;
        justify-content: center; }

    &__close {
        @include absolute(top 20px right 20px);
        @include size(50px);
        @include font(30px 50px white center);
        text-indent: -9999px;
        cursor: pointer;
        transition: all .25s ease;
        z-index: 100;

        &:hover {
            @include font(primary); }

        &::before {
            @include fa(times-circle);
            @extend %cover;
            text-indent: 0; } } }





